html,
body {
    margin: 0;
    padding: 0;
    font-family: 'Benton Sans', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.8);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 100%;
    width: 100%;
    overflow: hidden;
}

#container {
    height: 100%;
    width: 100%;
}

.container {
    padding: 0px 18px 0px 18px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
    height: 100%;
}

.header {
    font-family: 'Benton Sans';
    font-size: 16px;
    color: rgba(0, 0, 0, 0.8);
    margin-top: 12px;
    display: -ms-flexbox;
    display: flex;
}

.c {
    color: #C7C7C7;
    margin-left: 4px;
}

.cog {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
}

.cog p {
    width: 10%;
    margin: 10px;
    color: #C7C7C7;
    font-style: italic;
    width: 100px;
}

.title {
    margin-top: 18px;
}

.click {
    cursor: pointer;
}

.viewing {
    display: none;
}

.footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: end;
        align-items: flex-end;
    margin-top: 18px;
}

.btncluster {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: end;
        justify-content: flex-end;
    width: 100%;
    margin-bottom: 12px;
}


